@use 'sass:list';
@use 'sass:math';

body{
  overflow:hidden;
 background-color:black;
}
@function rand($val){
  $rand: math.random($val);
  @return $rand;
}

$slots: ();

@for $l from 1 through 100{
  $slots: list.append($slots, $l)
}
.drop{
  width:1px;
  border-radius: 50%;
  background: linear-gradient(rgba(255,255,255,.5), rgba(255,255,255,1));
  position:absolute;
  height:20px;
  top:-10vh;
  @for $i from 1 through list.length($slots){
    $randNo: rand(3*$i);
    &:nth-child(#{$i}){
      left: list.nth($slots, $i) + 0vw;
      $rand: math.random();
      transform:scaleY(rand(10)+50vh);
      filter: blur(2.5px);
      animation-delay: math.random()*10s;
      animation-duration: $rand*1s;
      opacity: $rand;
      animation-iteration-count: infinite;
      animation-timing-function:linear;
      animation-name:drop;
    }
  }
}
